@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* popular */
.popular {
	margin-bottom: $bottom-space--lg;
	background-color: $bg-color-base;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.popular__inner {
	position: relative;
	vertical-align: middle;
	color: $base-color-alt;
	text-decoration: none;
	text-align: center;
	padding: #{round($grid-space*1.5)} #{$grid-space};
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	z-index: 1;

	@if $has-animation {
		transition: {
			property: background-color, color;
			duration: $animation-speed; } }

	.no-touchevents &:hover {
		background-color: $base-color-alt;
		color: $bg-color-base; }

	@include media('<desk-lg') {
		padding: #{round($grid-space--lg*1.5)} #{$grid-space--lg}; }

	@include media('<mob-md') {
		display: block;
		padding: #{$grid-space--lg};
		text-align: left;
		direction: rtl; } }

.popular__text {
	display: block;
	@include font-medium;
	@include font(16px, round-float(19/16, 2));
	letter-spacing: 0.05em;
	text-transform: uppercase;
	margin-bottom: 20px;

	@include media('<desk-hd') {
		font-size: 14px;
		letter-spacing: 0.025em; }

	@include media('<mob-md') {
		@include display-inline-block-middle;
		margin: 0;
		width: calc(100% - 36px - 5px);
		padding-left: $grid-space--lg;
		direction: ltr; } }

.popular__icon {
	svg {
		@include display-inline-block-top;
		@include size(50px); }

	path {
		fill: $base-color; }

	@include media('<mob-md') {
		@include display-inline-block-middle;
		@include size(36px);

		svg {
			@include size(36px); } } }

/* popular end */
