@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* fame */

$fame-space: 80px;
$fame-list-space: 60px;
$fame-space--lg: 30px;
$fame-space--sm: 20px;

.fame {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.fame__list {
	@include grid-parent($fame-space);
	@include clear-spacing;
	margin-bottom: $bottom-space--md;

	@include media('<desk-hd') {
		@include grid-parent($fame-space--lg); }

	&:last-child {
		margin-bottom: 0; } }

.fame__item {
	position: relative;
	@include grid-item(3, $fame-space, round($fame-space*0.5));
	@include font(1.5rem, round-float(18px/15px, 2));
	letter-spacing: normal;

	@include media('<desk-hd') {
		@include grid-item-responsive(3, $fame-space--lg); }

	@include media('<desk-md') {
		@include grid-item-responsive(2, $fame-space--lg); }

	@include media('<desk-sm') {
		@include grid-item-responsive(3, $fame-space--lg); }

	@include media('<1100px') {
		@include grid-item-responsive(2, $fame-space--lg); }

	@include media('<tab-lg') {
		@include grid-item-responsive(3, $fame-space--lg); }

	@include media('<tab-sm') {
		@include grid-item-responsive(2, $fame-space--lg); }

	@include media('<mob-md') {
		@include grid-item-responsive(1, $fame-space--lg); } }

.fame__inner {
	display: block;
	height: 100%;
	text-decoration: none;
	color: inherit; }

.fame__card {
	margin: 0 0 3.8rem;
	max-width: 300px;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; } }

.fame__figure {
	position: relative;
	overflow: hidden;
	letter-spacing: normal;

	&::before {
		@include pseudo-block-relative();
		@include ratio(286, 410); }

	&::after {
		@include pseudo($content: 'i');
		right: $grid-space--sm;
		top: $grid-space--sm;
		@include size(32px);
		border-radius: 50%;
		background-color: $bg-color-base;
		@include font-extra-bold;
		@include font(24px, 32px);
		text-align: center;
		color: $text-color-base;
		z-index: 10;
		display: none;

		.touchevents & {
			display: block; } } }

.fame__img {
	display: block;
	overflow: hidden;
	@include cover-abs();
	font-size: $font-size;
	letter-spacing: normal;
	backface-visibility: hidden;
	//background: $border-color url(../../img/news-no-img.jpg) no-repeat bottom right
	//background-size: cover
	background-color: $border-color;
	text-decoration: none;
	color: inherit;
	will-change: transform;
	transform: translateZ(0);
	transition: {
		property: transform;
		duration: 0.4s;
		timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

	&::after {
		@include pseudo();
		@include position();
		@include size(100%);
		background-color: transparentize($base-color-alt, 0.5);
		opacity: 0;
		visibility: hidden;
		transition: {
			property: opacity, visibility;
			duration: 0.4s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	img {
		display: block;
		position: absolute;
		@include position(0);
		height: 100%;
		width: 100%;
		max-width: none;
		backface-visibility: hidden;
		@include object-fit(cover); } }

.fame__content {
	position: relative;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.fame__title, .fame__text {
	@include font-medium;
	@include font(16px, round-float(20/16, 2));
	letter-spacing: 0.025em;
	color: inherit;
	margin-bottom: 1rem;
	overflow: hidden;
	max-height: #{5 * 16px * round-float(24/16, 2)};
	transition: {
		property: color;
		duration: $animation-speed; }

	&:last-child {
		margin-bottom: 0; }

	.no-touchevents .fame__link:hover & {
		color: $base-color; } }

.fame__text {
	@include font-normal; }

.fame__info {
	position: absolute;
	width: 100%;
	max-height: 70%;
	overflow-x: hidden;
	overflow-y: auto;
	bottom: 0;
	background-color: white;
	//padding: #{round($grid-space*0.85)} #{$grid-space}
	padding: 6.5% 7%;
	box-shadow: inset 0 0 0 1px $border-color;
	will-change: transform;
	transform: translate(0, 100%) translateZ(0);
	transition: {
		property: transform;
		duration: 0.4s;
		timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

	.short-contacts {
		display: block;

		&, .#{$user-content} & {
			li {
				margin: 0 0 12px;
				padding: 0;

				&:last-child {
					margin-bottom: 0; } } }

		svg {
			@include size(18px); }

		a {
			line-height: 18px; } } }

// hover
.fame__figure {
	&.hover {

		.fame__img {
			transform: translate(0, -12%) translateZ(0);

			&::after {
				opacity: 1;
				visibility: visible; } }

		.fame__info {
			transform: translate(0, 0) translateZ(0); } } }


/* fame end */
